@import "fonts";
@import "libs";
@import "vars";

*::-webkit-input-placeholder {
	color  : #666;
	opacity: 1;
}
*:-moz-placeholder {
	color  : #666;
	opacity: 1;
}
*::-moz-placeholder {
	color  : #666;
	opacity: 1;
}
*:-ms-input-placeholder {
	color  : #666;
	opacity: 1;
}
body input:focus:required:invalid,
body textarea:focus:required:invalid {
	color: #666;
}
body input:required:valid,
body textarea:required:valid {
	color: #666;
}
body,
html {
	height: 100%;
}
body {
	font-size  : 16px;
	min-width  : 320px;
	position   : relative;
	line-height: 1.75;
	font-family: $default-font;
	overflow-x : hidden;
	opacity    : 1;
}
.img-responsive {
	display  : block;
	max-width: 100%;
	height   : auto;
}
.ms-phone {
	color: #fff;
	.uk-icon {
		color: #fff;
	}
}
.ms-header-wrap {
	color: #fff;
	.ms-section-top-nav {

		.uk-navbar-nav {
			& > li:hover > a {
				color: #fff;
			}
			li.uk-active > a {
				position: relative;
				&::before {
					content            : '';
					display            : block;
					position           : absolute;
					left               : 15px;
					right              : 15px;
					bottom             : 20px;
					height             : 1px;
					background-color   : currentColor;
					transition         : 0.3s ease-in-out;
					transition-property: right;
				}
			}
			li > a {
				position: relative;
				color   : rgba(255,255,255,.8);
				&::before {
					content            : '';
					display            : block;
					position           : absolute;
					left               : 15px;
					right              : calc(102% - 15px);
					bottom             : 20px;
					height             : 1px;
					background-color   : currentColor;
					transition         : 0.3s ease-in-out;
					transition-property: right;
				}
				&:hover::before {
					right: 15px;
				}
			}
		}
	}

	.ms-section-header {
		padding-bottom: 70px;
		.ms-header-title {
			padding-top: 30px;
		}
		.ms-header-promo {
			margin-top: 10px;
			color     : #444;
		}
	}
}

.ms-section-title {
	text-transform: uppercase;
	font-weight   : 600;
}
@import "media"; // Всегда в конце
